.song-container {
    min-height: 60vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    img {
        width: 20%;
        border-radius: 2%;
        box-shadow:  29px 29px 70px #bebebe,
             -29px -29px 70px #ffffff;
    }

    h2 {
        padding: 3rem 1rem 1rem 1rem; 
    }

    h3 {
        font-size: 1rem;
    }
}

@media screen and (max-width: 768px) {
    .song-container {
        img {
            width: 60%;
        }
    }
}