.player-container {
    min-height: 20vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    .time-control {
        width: 50%;
        display: flex;
        align-items: center;
    
        input {
            width: 100%;
            padding: 0;
            -webkit-appearance: none;
            background: transparent;
            cursor: pointer;
        }
        
        p {
            padding: 1rem;
        }
    }

    .play-control {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 1rem;
        width: 30%;

        svg {
            cursor: pointer;
        }
    }
}

input[type='range']:focus {
    -webkit-appearance: none;
    background: transparent;
    cursor: pointer;
}

input[type='range']::-webkit-slider-thumb {
    -webkit-appearance: none;
    height: 12px;
    width: 12px;
}

input[type='range']::-moz-slider-thumb {
    -webkit-appearance: none;
    background: transparent;
    border: none;
}

.track {
    background: lightblue;
    width: 100%;
    height: 1rem;
    position: relative;
    border-radius: 1rem;
    overflow: hidden;
}

.animate-track{
    background: whitesmoke;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transform: translateX(0%);
    pointer-events: none;
}


@media screen and (max-width: 768px) {
 .player-container{
    .time-control {
        width: 90%;
    }
    .play-control{
        width: 50%;
    }
 }
}